import React from 'react'
import './CardPresentation.css'
function CardPresentation({cardName, imageURL, collectionName,alt}){
    return(
        <div className = "container">
            <div className = 'card-container'>
                <div className = "components-container">
                    <div className = "image-container" >
                        <img className = "card-Image" src = {`${process.env.PUBLIC_URL}${imageURL}`} alt = {alt}/> 
                    </div>
                    <div className = "card-cardNameContainer">
                        <h3>{cardName}</h3>
                    </div>
                    
                    <div className = "card-collectionNameContainer">
                        <div className = "card-collectionName">
                            <h3> {collectionName}</h3>
                        </div>
                        
                    </div>

                    <div className = "buyButton"> 
                        <button>
                            <div>
                                Comprar
                            </div>
                        </button>
                    </div>
                </div>
            </div>

        </div>    

        
    )
}
export default CardPresentation