import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { FaBars, FaTimes } from 'react-icons/fa';
import RaptorLogo from './Icons/RaptorLogo';
import { IconContext } from 'react-icons/lib';
import './Navbar.css';

function Navbar() {
    const [click, setclick] = useState(false);

    const handleClick = () => setclick(!click);
    const closeMobileMenu = () => setclick(false);

    return (
        <>
        <IconContext.Provider value={{color:'#000'}}>
          <div className="navbar">
              <div className="navbar-container container">

                  <Link to='/ntfs-test' className='navbar-logo' onClick={closeMobileMenu}>
                      <RaptorLogo  className='navbar-icon'/> <p className='navbar-title'>Raptor NTF</p>
                  </Link>

                  <div className="menu-icon" onClick={handleClick}>
                      {click ? <FaTimes/> :<FaBars/>}
                    </div>


                    <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                        <li className='nav-item'>
                            <Link to='/ntfs-test' className='nav-links' onClick={closeMobileMenu}>
                                Inicio
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/inventario' className='nav-links' onClick={closeMobileMenu}>
                                Inventario
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/acerca-de' className='nav-links' onClick={closeMobileMenu}>
                                Acerca de
                            </Link>
                        </li>
                    </ul>
              </div>
            </div>
        </IconContext.Provider>  
        </>
    )
}
export default Navbar
