import React from 'react';
import './Footer.css';
import { Link } from 'react-router-dom';
import {
  FaFacebook,
  FaInstagram,
  FaYoutube,
  FaTwitter,
  FaTiktok
} from 'react-icons/fa';
import RaptorLogo from '../../Icons/RaptorLogo';

function Footer() {
  return (
    <div className='footer-container'>
      <section className='social-media'>
        <div className='social-media-wrap'>
          <div className='footer-logo'>
            <Link to='/' className='social-logo'>
            <RaptorLogo  className='navbar-icon'/>
              Raptor NTF
            </Link>
          </div>
          <small className='website-rights'>MAJIC © 2020</small>
          <div className='social-icons'>
            <Link
              className='social-icon-link'
              to={'////www.facebook.com/RaptorGamerYT'}
              target='_blank'
              aria-label='Facebook'
            >
              <FaFacebook />
            </Link>
            <Link
              className='social-icon-link'
              to={'//www.instagram.com/raptorgamer_/?hl=es-la'}
              target='_blank'
              aria-label='Instagram'
            >
              <FaInstagram />
            </Link>
            <Link
              className='social-icon-link'
              to={
                '//www.youtube.com/c/RaptorGamer'
              }
              target='_blank'
              aria-label='Youtube'
            >
              <FaYoutube />
            </Link>
            <Link
              className='social-icon-link'
              to={'//twitter.com/Raptorgamer_'}
              target='_blank'
              aria-label='Twitter'
            >
              <FaTwitter />
            </Link>
            <Link
              className='social-icon-link'
              to={'//www.tiktok.com/@raptorgameryt'}
              target='_blank'
              aria-label='LinkedIn'
            >
              <FaTiktok />
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;