import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
// import { FaFire } from 'react-icons/fa';
// import { Button } from '../../Button';
// import { BsXDiamondFill } from 'react-icons/bs';
// import { GiCrystalize } from 'react-icons/gi';
import { IconContext } from 'react-icons/lib';
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css"
import leftArrow from "../../Icons/left-arrow.png"
import rightArrow from "../../Icons/right-arrow.png"
import './Pricing.css';
import {items , responsive} from '../Inventario/Data'

class Pricing extends Component {
    render() {
    return (
        <div className="test">
            <IconContext.Provider value={{color:'#fff', size: '64'}}>
       <h1 className='heading'>Galeria de cartas</h1>
            <div className="pricing__section">
  <div className="App-Container">
        <div className = "LeftButton" onClick ={() => this.carouselRef.slidePrev()}>
        <div  className = "botonIzquierdo">
            <img src = {leftArrow} className = "img_boton_izquierdo" alt = "boton-izquierdo"/>
        </div>
        </div>  
        <div className = "jumbotron">
                <AliceCarousel ref = {(el) => this.carouselRef = el}  disableButtonsControls disableDotsControls className = "AliceCarousel" autoPlay = {true} autoPlayInterval = {2000} mouseDragEnabled = {false} mouseTracking items = {items} responsive = {responsive} infinite = "true" /> 
                </div> 
        <div className = "RightButton" onClick = {() => this.carouselRef.slideNext()}>
          <div className = "botonDerecho">
              <img src = {rightArrow} className = "img_boton_derecho" alt = "boton-derecho" /></div>
        </div>   
       

      </div>
            </div>
            </IconContext.Provider>
        </div>
    );
        }
}

export default Pricing
